function ready() {
	console.log(window.location.pathname)
	switch(window.location.pathname) {
		case '/': {
			authorization();
			break;
		}
		case '/chat': {
			chat();
			break;
		}
		case '/registration': {
			registration();
			break;
		}
		case '/profile': {
			profile();
			break;
		}
		default:{
			error();
		}
	}
}

document.addEventListener('DOMContentLoaded', ready);
